.pagination-list, .pagination-list-ing {
  justify-content: center;
  align-items: center;
  column-gap: 8px;
  margin-bottom: 50px;
  display: flex;
}

.numb, .dots, .prev, .next {
  color: var(--primary-accent-color);
  width: 32px;
  height: 32px;
  border: 1px solid var(--primary-accent-color);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  display: flex;
}

.prev {
  margin-right: 31px;
}

.next {
  margin-left: 31px;
}

.active-pagination, .numb:hover, .prev:hover, .next:hover {
  cursor: pointer;
  background-color: var(--secondary-accent-color);
  color: var(--primary-text-color-theme-dark);
  border: none;
}

@media screen and (min-width: 768px) {
  .pagination-list, .pagination-list-ing {
    column-gap: 14px;
  }

  .numb, .dots, .prev, .next {
    width: 40px;
    height: 40px;
    font-size: 16px;
  }

  .prev {
    width: 60px;
    height: 40px;
    margin-right: 94px;
  }

  .next {
    width: 60px;
    height: 40px;
    margin-left: 94px;
  }
}

@media screen and (min-width: 1280px) {
  .prev {
    margin-right: 184px;
  }

  .next {
    margin-left: 184px;
  }
}

